.brand-main {
  img {
    height: 25px;
    padding-top: 6px;
  }
}

.nav-toggle {
  background-color: $header-buttons-bg-color;
  border-radius: 2px;
  color: $header-buttons-font-color;
  display: none;
  font-size: 14px;
  line-height: 14px;
  padding: 6px;
  &:active,
  &:focus,
  &:visited {
    color: $header-font-color;
  }
}

.action-list {
  float: right;
  list-style: none;
  margin: 0;

  li {
    display: block;
    float: left;
    position: relative;
    height: 36px;
    margin: 0;
    padding: 0 4px;

    a {
      background-color: $header-buttons-bg-color;
      border-radius: 2px;
      color: $header-buttons-font-color;
      display: inline-block;
      font-size: 14px;
      line-height: 14px;
      padding: 11px;
    }

    a:hover {
      background-color: $header-buttons-bg-color--hover;
      color: $header-font-color;
    }

  }

  .avatar{
    padding: 0;
    background-color: transparent;
  }

  .avatar img {
    width: 36px;
    border-radius: 2px;
  }

  .dropdown-menu {
    background-color: $header-buttons-bg-color;

    i{
      width: 25px;
      text-align: center;
      margin-left: -5px;
    }

  }

  .notification-dropdown {
    padding: 5px;

    .dropdown-header {
      border-bottom: 1px solid $ls-color-gray--very-dark;
      color: $ls-color-primary;
      padding: 3px 10px 8px;
      margin-bottom: 0;
    }
  }

  .dropdown-divider {
    background-color: $dropdown-divider-bg;
  }

}

@include media-breakpoint-down(sm) {

  .nav-toggle {
    display: inline-block;
    margin-left: 10px;
  }

  .brand-main {
    float: left;

    img {
      height: 32px;
      padding-top: 4px;
    }
  }
}
